
.navbar-brand{
    height: 100%;
    padding: 0px;
    border: 0px;
}

a.navbar-brand img{
    height: 9vh;
    padding-left: 20px;

}

nav.navbar.navbar-expand-lg.navbar-dark.bg-primary.sticky{
    background-color: transparent;
}

.navbar{
    border: 0px;

}

.headroom {
    top: 0;
    left: 0;
    right: 0;
    z-Index: 999;
  }
  .headroom--unfixed {
    position: relative;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
  }
  .headroom--scrolled {
    -webkit-transition: -webkit-transform 0ms ease-in-out;
    transition: -webkit-transform 0ms ease-in-out;
    -o-transition: transform 0ms ease-in-out;
    transition: transform 0ms ease-in-out;
    transition: transform 0ms ease-in-out, -webkit-transform 0ms ease-in-out;
  }
  .headroom--unpinned {
    position: fixed;
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  .headroom--pinned {
    position: fixed;
    -webkit-transform: translateY(0%);
        -ms-transform: translateY(0%);
            transform: translateY(0%);
  }


